import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
	state: {
		localStorage: JSON.parse(localStorage.getItem("o.pass")) || {
			registration: false
		},
		temporary: {
			PIN: "",
			authorization: false
		}
	},
	mutations: {
		editPIN(state, number) {
			if (number) {
				if (state.temporary.PIN.length < 8) {
					state.temporary.PIN += number;
				}
			} else {
				state.temporary.PIN = state.temporary.PIN.substring(0, state.temporary.PIN.length - 1);
			}
		},
		save: state => {
			localStorage.setItem("o.pass", JSON.stringify(state.localStorage));
		},
		registration: state => {
			let PIN = state.temporary.PIN
			if (
				PIN.length <= 8 &&
				PIN.length >= 4
			) {
				if (Number(PIN)) {
					state.localStorage.registration = true;
					state.temporary.authorization = true;

					return true;
				}

				return false;
			}

			return false;
		}
	},
	actions: {
		registration(context) {
			context.commit("registration")
			if (context.state.localStorage.registration) {
				context.commit("save");
			}
		}
	},
	modules: {},
	getters: {}
});
